.expense-date {
    display: flex;
    flex-direction: column;
    min-width: 15vw;
    height: 5rem;
    border: 1px solid #ececec;
    background-color: #2a2a2a;
    color: white;
    border-radius: 12px;
    align-items: center;
    justify-content: center;
  }
  
  .expense-date__month {
    font-size: 0.75rem;
    font-weight: bold;
  }
  
  .expense-date__year {
    font-size: 0.75rem;
  }
  
  .expense-date__day {
    font-size: 1.5rem;
    font-weight: bold;
  }

  @media (max-width:300px){
    .expense-date{
      flex-direction: row;
      gap:0.4rem;
      height:150%;
      width:80%;
    }

    .expense-date-year_month{
      align-items: center;
    }
  }