@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  margin: 0;
  /* background-color: #221f1f; */
  background-color: white;
}


.expense-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.3vw;
    margin: 0.8rem 0;
    background-color:white;
    color:#333;

  }
  
  .expense-item__description {
    display: flex;
    grid-gap: 2vw;
    gap: 2vw;
    align-items: center;
    justify-content: flex-start;
    flex: 1 1;
  }
  
  .expense-item h2 {
    font-size: 1rem;
    flex: 1 1;
    margin: 0 1rem;
  }
  
  .expense-item__price {
    font-size: 1rem;
    font-weight: bold;
    background-color: #a892ee;
    border: 1px solid white;
    /* color:white; */
    padding: 0.3rem;
    border-radius: 12px;
  }
  
  @media (max-width: 400px) {
    .expense-item__description {
      flex-direction: column;
      align-items: center;
      justify-content: flex-start;
      flex: 1 1;
    }

  }

  @media (max-width:300px){
    .expense-item{
      flex-direction: column;
    }
  }
.expense-date {
    display: flex;
    flex-direction: column;
    min-width: 15vw;
    height: 5rem;
    border: 1px solid #ececec;
    background-color: #2a2a2a;
    color: white;
    border-radius: 12px;
    align-items: center;
    justify-content: center;
  }
  
  .expense-date__month {
    font-size: 0.75rem;
    font-weight: bold;
  }
  
  .expense-date__year {
    font-size: 0.75rem;
  }
  
  .expense-date__day {
    font-size: 1.5rem;
    font-weight: bold;
  }

  @media (max-width:300px){
    .expense-date{
      flex-direction: row;
      grid-gap:0.4rem;
      gap:0.4rem;
      height:150%;
      width:80%;
    }

    .expense-date-year_month{
      align-items: center;
    }
  }
.card{
    border-radius: 12px;
    box-shadow: 0 2px 3px rgba(205, 205, 205, 0.25);
}


.expenses-filter {
    color: white;
    padding: 0 1vw;
  }
  
  .expenses-filter__control {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-between;
    margin: 2vh 0;
  }
  
  .expenses-filter label {
    font-weight: bold;
    margin-bottom: 0.5vw;
  }
  
  .expenses-filter select {
    font: inherit;
    padding: 0.5vw 3vh;
    font-weight: bold;
    border-radius: 6px;
  }

.chart-bar {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .chart-bar__inner {
    height: 100%;
    width: 13px;
    border: 1px solid #313131;
    border-radius: 12px;
    background-color: #c3b4f3;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
  }
  
  .chart-bar__fill {
    background-color: #4826b9;
    width: 100%;
    transition: all 0.3s ease-out;
  }
  
  .chart-bar__label {
    font-weight: bold;
    font-size: 0.5rem;
    text-align: center;
  }

  @media (max-width:400px){
    .chart-bar{
      flex-direction: row-reverse;
      height:13px;
      width:100%;
      justify-content:space-between;
    }
    
    .chart-bar__inner{
      width:100px;
    }

  }
.chart {
    padding: 1rem;
    border-radius: 12px;
    background-color: #f8dfff;
    text-align: center;
    display: flex;
    justify-content: space-around;
    height: 12rem;
  }

  @media (max-width:400px){
    .chart{
      flex-direction: column;
      justify-content:flex-start;
      grid-gap:0.2rem;
      gap:0.2rem;
    }
  }
.expenses{
    display: block;
    margin: 0px auto;
    padding:2vh;
    /* background-color:rgb(37, 37, 37); */
    background-color:#77c5e4;
    width:60%;

}
.new-expense__controls {
    display: flex;
    flex-wrap: wrap;
    grid-gap: 1vw;
    gap: 1vw;
    margin-bottom: 1vh;
    text-align:left;
    /* width:30%; */
  }
  
  .new-expense__control label {
    font-weight: bold;
    margin-bottom: 0.5vh;
    display: block;
  }
  
  .new-expense__control input {
    font: inherit;
    padding: 0.5rem;
    border-radius: 6px;
    border: 1px solid #ccc;
    width: 27vw;
    max-width: 90%;
  }
  
  .new-expense__actions {
    text-align: right;
  }

  .new-expense__actions button{
    margin-top:3vh;
  }


@media (max-width:550px) {
  .new-expense__controls{
    flex-wrap: nowrap;
    flex-direction: column;
    align-items: center;
  }

  .new-expense__control{
    width:70%;
  } 

  .new-expense__control input{
    width:100%;
  }

  .new-expense__actions {
      text-align:center;
  }
}
.new-expense {
    background-color: #a892ee;
    padding: 1vw;
    margin: 2vw auto;
    width: 60%;
    max-width: 90%;
    border-radius: 12px;
    text-align: center;
    box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
  }
  
  .new-expense button {
    font: inherit;
    /* font-size:4.5vw; */
    cursor: pointer;
    padding: 1vh 2vw;
    border: 1px solid #40005d;
    background-color: #40005d;
    color: white;
    border-radius: 12px;
    margin-right: 1vw;
    
  }
  
  .new-expense button:hover,
  .new-expense button:active {
    background-color: #510674;
    border-color: #510674;
  }

